.Folder {
  width: 250px;
  padding: 16px;
  text-align: center;
  border: 1px solid #eee;
  box-shadow: 0 2px 3px #ccc;
  margin: 10px;
  box-sizing: border-box;
  cursor: pointer;
}

.Folder:hover,
.Folder:active {
  background-color: #c0ddf5;
}

.Folders li {
  list-style-type: none;
}
